import Vue from 'vue';
import VueRouter from 'vue-router';

import Index from '../views/index/Index'; // 首页
import MeetingManage from '../views/meetingManage/Index'; // 会议管理
import SignInDetails from '../views/meetingManage/SignInDetails'; // 签到详情

import PersonnelManage from '../views/personnelManage/Index'; // 人事管理
import MyArchives from '../views/personnelManage/MyArchives'; // 我的档案
import CreateNewUser from '../views/personnelManage/CreateNewUser'; // 新建人员

import ProcessManage from '../views/processManage/Index'; // 流程管理
import ProcessList from '../views/processManage/ProcessList'; // 代办事宜
import ProcessSearch from '../views/processManage/Search'; // 高级搜索
import CreateProcess from '../views/processManage/CreateProcess'; // 新建流程

import CarManage from '../views/carManage/Index'; // 车辆管理
import CreateUseCar from '../views/carManage/CreateUseCar'; // 创建用车申请

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        name: 'Index',
        component: Index
    },
    {
        path: '/meetingManage',
        name: 'MeetingManage',
        component: MeetingManage
    },
    {
        path: '/createUseCar',
        name: 'CreateUseCar',
        component: CreateUseCar
    },
    {
        path: '/signInDetails',
        name: 'SignInDetails',
        component: SignInDetails
    },
    {
        path: '/personnelManage',
        name: 'PersonnelManage',
        component: PersonnelManage
    },
    {
        path: '/myArchives',
        name: 'MyArchives',
        component: MyArchives
    },
    {
        path: '/createNewUser',
        name: 'CreateNewUser',
        component: CreateNewUser
    },
    {
        path: '/processManage',
        name: 'ProcessManage',
        component: ProcessManage
    },
    {
        path: '/processList',
        name: 'ProcessList',
        component: ProcessList
    },
    {
        path: '/processSearch',
        name: 'ProcessSearch',
        component: ProcessSearch
    },
    {
        path: '/createProcess',
        name: 'CreateProcess',
        component: CreateProcess
    },
    {
        path: '/carManage',
        name: 'CarManage',
        component: CarManage
    }
];

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
});

export default router;
